<template>
 <v-app>
    <v-content>

      <GmapMap
      :center="mapCenter"
      :zoom="mapZoom"
      map-type-id="roadmap"
      style="width: 100%; display: block; height: 100%;"
      :options="mapOptions" >
        <GmapMarker
        :key="index"
        v-for="(m, index) in markers"
        :position="m.position"
        :icon="getIcon(m)"
        alignment="center"
        @click="display(index)"
        />
        </GmapMap>

    </v-content>


    <v-dialog v-model="dialog" fullscreen >
      <v-toolbar color="teal lighten-1 " dark >
            <v-btn icon @click.native="dialog = false" dark>
              <v-icon>close</v-icon>
            </v-btn>
            <v-toolbar-title>{{station.name}}</v-toolbar-title>
      </v-toolbar>
      <v-card >
            <v-card-text>

          <div class="text-xs-center">
            <h1 class="mb-2 display-1 text-xs-center">{{ getDescription(station)}}</h1>
          </div>
            </v-card-text>



            <v-layout column wrap  align-center >
            <v-container grid-list-xl>
                <v-layout row wrap align-center>

                  <v-flex xs6 md3>
                    <v-card-text>
                      <img :src="getIconPM25(station)"  :style="{ cursor: 'pointer'}">
                    </v-card-text>
                  </v-flex>

                  <v-flex xs6 md3>
                    <v-card-text>
                      <img :src="getIconAQI(station)"  :style="{ cursor: 'pointer'}">
                    </v-card-text>
                  </v-flex>

                  <v-flex xs6 md3>
                    <v-card-text>
                      <img :src="getIconTemp(station)">
                    </v-card-text>
                  </v-flex>
                
                  <v-flex xs6 md3>
                    <v-card-text>
                      <img :src="getIconHumi(station)">
                    </v-card-text>
                  </v-flex>

                
                </v-layout>
            </v-container>
            </v-layout>

                  <v-layout xs12 wrap>
                      <bar-chart :chart-data="datacollection" :options="chart_options" style="height:40vh;width:200vw"></bar-chart>
                  </v-layout>

                  <v-layout xs12 wrap>
                    <v-flex text-xs-center>
                      <v-btn round color="primary" dark @click="getRawPM25">PM2.5</v-btn>
                      <v-btn round color="primary" dark @click="getRawAQI">AQI</v-btn>
                    </v-flex>
                  </v-layout>

                  <v-layout xs12 wrap>
                    <v-flex text-xs-center>
                      <v-chip color="secondary" text-color="white">Last Update : {{ station.dtm}} </v-chip>
                    </v-flex>
                  </v-layout>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn round color="black" flat @click="dialog = false">Close</v-btn>
              </v-card-actions>

        
      </v-card>
    </v-dialog>
    </v-app>
</template>


<script>
    import Vue from 'vue'
    import * as VueGoogleMaps from 'vue2-google-maps'
    import VueAnalytics from 'vue-analytics'
    //import LineChart from './LineChart.js'
    import BarChart from './BarChart.js'

import axios from 'axios'
// import moment from 'moment'

    Vue.use(VueAnalytics, {
    id: 'UA-136776555-1'
    })


 Vue.use(VueGoogleMaps, {
   load: {
     key: 'AIzaSyAP643J8qbUflgApZNfsbfUeA1r3pZ5AGs',
     libraries: 'places', // This is required if you use the Autocomplete plugin
     // OR: libraries: 'places,drawing'
     // OR: libraries: 'places,drawing,visualization'
     // (as you require)

     //// If you want to set the version, you can do so:
     // v: '3.26',
   },

   //// If you intend to programmatically custom event listener code
   //// (e.g. `this.$refs.gmap.$on('zoom_changed', someFunc)`)
   //// instead of going through Vue templates (e.g. `<GmapMap @zoom_changed="someFunc">`)
   //// you might need to turn this on.
   // autobindAllEvents: false,

   //// If you want to manually install components, e.g.
   //// import {GmapMarker} from 'vue2-google-maps/src/components/marker'
   //// Vue.component('GmapMarker', GmapMarker)
   //// then disable the following:
   // installComponents: true,
 })



export default {
  name: 'map',
  components: {
      BarChart
    },
    data() {
        return {
            //api_url :'http://192.168.10.203:8081/api/v1/station',
            api_url :'https://www.phitloktoday.com/api/v1/',
            // api_url :'https://www.iotplus.cloud/api/v1/station',
            markers :[],
            //     {aqi:109, position: { lat:16.808225,lng:100.291653}},
            //     {aqi:110, position: { lat:16.818225,lng:100.371653}},
            //     {aqi:111, position: { lat:16.798225,lng:100.201653}},
            //     {aqi:112, position: { lat:16.758225,lng:100.271653}},
            //     {aqi:113, position: { lat:16.845225,lng:100.271653}}
            // ],

            srcicon:[],
            mapOptions:{
                disableDefaultUI : true,
                gestureHandling:'greedy'
            },
            dialog:false ,
            dialog_color:"rgb(168,224,95)",
            text_color:"rgb(255,255,255)",
            station_index : 0,
            station :{
              name : "--",
              serial : "--",
              pm25 : "--",
              pm10 : "--",
              temp : "--",
              humi : "--",
              dtm : "--",
            },
            bg_colors : ["rgb(218,218,218)","rgb(47,155,0)", "rgb(168,224,95)", "rgb(255,215,0)", "rgb(255,169,104)", "rgb(254,106,105)"],
            txt_colors : ["rgb(140,140,140)","rgb(106,236,50)", "rgb(0,139,0)", "rgb(165,127,35)", "rgb(178,34,34)", "	rgb(175,44,59)"],
            style_modal : '',
            menuItems: [
                // {icon: 'map', title: 'Map', link: '/map'},
                // {icon: 'people', title: 'Sponser', link: '/sponser'},
                //{icon: 'person', title: 'Profile', link: '/profile'},
                // {icon: '', title: 'About Us',  link: '/aboutus'}
            ],
            // LPN
            // mapCenter: { lat: 13.7124112, lng: 100.6060072 },
            // mapZoom : 15

            // PHS
            mapCenter : {lat:16.818225, lng:100.271653},
            mapZoom : 12,
            aqi_mode : "th",
            display_mode : 1,   // 1=pm25 (realtie) , 2=aqi  (avg 24 hr)
            labels: [ '12am', '3am', '6am', '9am', '12pm', '3pm', '6pm', '9pm' , '12am', '3am', '6am', '9am', '12pm', '3pm', '6pm', '9pm' ],
            value: [ 200, 675, 410, 390, 310, 460, 250, 240  ,200, 675, 410, 390, 310, 460, 250, 240 ],
            datacollection: null,
            chart_options : {
              maintainAspectRatio:false,
              // responsive : true,
              legend:{
                // display: false
              },
              scales: {
                xAxes: [{
                    barPercentage: 1,
                    categoryPercentage: 1,
                    display: false
                }],
                yAxes: [{
                barPercentage: 1,
                    categoryPercentage: 1,
                    ticks: {
                        beginAtZero: true
                    }
                }]
              },
            }


      }
    },
    mounted () {

        this.loadStation()
        setInterval(
            function(){
                this.loadStation()
            }.bind(this)
            ,60000
        )
        // this.renderChart(data, options)
    },
    methods : {
        getStationAQIColor(type){
            if (type == "bg")
                return this.bg_colors[this.getLevel(this.station)]
            else
                return "color: " + this.txt_colors[this.getLevel(this.station)]
        },
        display(i){
            this.dialog = true
            this.station_index = i
            this.station = this.markers[i]

            this.dialog_color = this.bg_colors[this.station.level]
            this.text_color = this.txt_colors[this.station.level]
            this.getRawPM25()
        },
        loadStation(){
            axios.post(this.api_url + 'station',).then((response)=>{
                this.markers = []
                
                response.data.forEach((item)=>{
                    this.markers.push({
                        id:item.id,
                        th_aqi:item.th_aqi,
                        ch_aqi:item.ch_aqi,
                        us_aqi:item.us_aqi,
                        pm25_avg:item.pm25_avg,
                        pm10:item.pm10,
                        pm25:item.pm25,
                        temp:item.temp,
                        humi:item.humi,
                        serial:item.serial,
                        name:item.name,
                        status:item.status,
                        dtm:item.dtm,
                        position:{ lat:parseFloat(item.lat), lng:parseFloat(item.lng)}
                    })
                    this.station = this.markers[this.station_index]
                })
            })
        },
        getIcon(m){

            // var aqi_rlt = this.getScorePM25(m.pm25,"th",m.status)

            return 'data:image/svg+xml;utf-8, \
            <svg width="40px" height="40px" viewBox="0 0 54.4 54.4" xmlns="http://www.w3.org/2000/svg"> \
                <circle  fill="'+ this.bg_colors[this.getLevel(m)]+'" cx="27.2" cy="27.2" r="27.2"/> \
                <text dx="27" dy="35" text-anchor="middle" fill="'+ this.txt_colors[this.getLevel(m)]+'" style="font-size:20px;  font-family: Arial, Verdana; font-weight: bold">'+this.getText(m)+'</text>\
            </svg>'


        },
        getIconPM25(m){
          
            return 'data:image/svg+xml;utf-8, \
            <svg width="100%" height="100%" viewBox="0 0 54.4 54.4" xmlns="http://www.w3.org/2000/svg"> \
                <circle  fill="'+ this.bg_colors[this.getLevel(m)]+'" cx="27.2" cy="27.2" r="27.2"/> \
                <text dx="27" dy="30" text-anchor="middle" fill="'+ this.txt_colors[this.getLevel(m)]+'" style="font-size:20px;  font-family: Arial, Verdana; font-weight: bold">'+ m.pm25 +'</text>\
                <text dx="27" dy="39" text-anchor="middle" fill="'+ this.txt_colors[this.getLevel(m)]+'" style="font-size:5px;  font-family: Arial, Verdana; font-weight: bold">PM2.5 Realtime</text>\
            </svg>'

        },
        getIconAQI(m){
            return 'data:image/svg+xml;utf-8, \
            <svg width="100%" height="100%" viewBox="0 0 54.4 54.4" xmlns="http://www.w3.org/2000/svg"> \
                <circle  fill="'+ this.bg_colors[this.getLevelAQIVal(m)]+'" cx="27.2" cy="27.2" r="27.2"/> \
                <text dx="27" dy="30" text-anchor="middle" fill="'+ this.txt_colors[this.getLevelAQIVal(m)]+'" style="font-size:20px;  font-family: Arial, Verdana; font-weight: bold">'+m.th_aqi+'</text>\
                <text dx="27" dy="39" text-anchor="middle" fill="'+ this.txt_colors[this.getLevelAQIVal(m)]+'" style="font-size:8px;  font-family: Arial, Verdana; font-weight: bold">TH AQI</text>\
            </svg>'

        },
        getIconTemp(m){
            return 'data:image/svg+xml;utf-8, \
            <svg width="100%" height="100%" viewBox="0 0 54.4 54.4" xmlns="http://www.w3.org/2000/svg"> \
                <circle  fill="'+ this.bg_colors[1]+'" cx="27.2" cy="27.2" r="27.2"/> \
                <text dx="33" dy="30" text-anchor="middle" fill="'+ this.txt_colors[1]+'" style="font-size:20px;  font-family: Arial, Verdana; font-weight: bold">'+ m.temp +'</text>\
                <text dx="33" dy="39" text-anchor="middle" fill="'+ this.txt_colors[1]+'" style="font-size:8px;  font-family: Arial, Verdana; font-weight: bold">°C</text>\
                <svg width="30%" height="46%" x="5" y="16" aria-hidden="true" focusable="false" data-prefix="fas" data-icon="thermometer-half" class="svg-inline--fa fa-thermometer-half fa-w-8" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 256 512"><path fill="'+ this.txt_colors[1]+'" d="M192 384c0 35.346-28.654 64-64 64s-64-28.654-64-64c0-23.685 12.876-44.349 32-55.417V224c0-17.673 14.327-32 32-32s32 14.327 32 32v104.583c19.124 11.068 32 31.732 32 55.417zm32-84.653c19.912 22.563 32 52.194 32 84.653 0 70.696-57.303 128-128 128-.299 0-.609-.001-.909-.003C56.789 511.509-.357 453.636.002 383.333.166 351.135 12.225 321.755 32 299.347V96c0-53.019 42.981-96 96-96s96 42.981 96 96v203.347zM208 384c0-34.339-19.37-52.19-32-66.502V96c0-26.467-21.533-48-48-48S80 69.533 80 96v221.498c-12.732 14.428-31.825 32.1-31.999 66.08-.224 43.876 35.563 80.116 79.423 80.42L128 464c44.112 0 80-35.888 80-80z"></path></svg>\
            </svg>'

        },
        getIconHumi(m){
            return 'data:image/svg+xml;utf-8, \
            <svg width="100%" height="100%" viewBox="0 0 54.4 54.4" xmlns="http://www.w3.org/2000/svg"> \
                <circle  fill="'+ this.bg_colors[1]+'" cx="27.2" cy="27.2" r="27.2"/> \
                <text dx="35" dy="33" text-anchor="middle" fill="'+ this.txt_colors[1]+'" style="font-size:20px;  font-family: Arial, Verdana; font-weight: bold">'+ m.humi +'</text>\
                <text dx="35" dy="39" text-anchor="middle" fill="'+ this.txt_colors[1]+'" style="font-size:5px;  font-family: Arial, Verdana; font-weight: bold">Humidity</text>\
                <svg width="48%" height="48%" x="0" y="15" viewBox="-64 0 512 512"><path fill="'+ this.txt_colors[1]+'" d="m208.421875 7.546875c-8.398437-10.0625-24.359375-10.0625-32.757813 0-17.980468 21.53125-175.664062 213.253906-175.664062 312.410156 0 105.890625 86.152344 192.042969 192.042969 192.042969 105.894531 0 192.046875-86.152344 192.046875-192.042969 0-99.15625-157.6875-290.878906-175.667969-312.410156zm-16.378906 461.777344c-82.359375 0-149.367188-67.003907-149.367188-149.367188 0-63.628906 96.261719-198.015625 149.367188-264.894531 53.105469 66.878906 149.367187 201.265625 149.367187 264.894531 0 82.363281-67.007812 149.367188-149.367187 149.367188zm0 0"/><path fill="'+ this.txt_colors[1]+'" d="m112.941406 347.527344 128.027344-128.027344 30.175781 30.175781-128.027343 128.027344zm0 0"/><path fill="'+ this.txt_colors[1]+'" d="m170.707031 255.941406c0 11.785156-9.554687 21.339844-21.339843 21.339844-11.785157 0-21.335938-9.554688-21.335938-21.339844s9.550781-21.339844 21.335938-21.339844c11.785156 0 21.339843 9.554688 21.339843 21.339844zm0 0"/><path  fill="'+ this.txt_colors[1]+'" d="m256.058594 341.292969c0 11.785156-9.554688 21.339843-21.339844 21.339843-11.78125 0-21.335938-9.554687-21.335938-21.339843 0-11.785157 9.554688-21.335938 21.335938-21.335938 11.785156 0 21.339844 9.550781 21.339844 21.335938zm0 0"/></svg>\
            </svg>'

        },
        getLevelPM25(item) {
            if (item.status == 0) 
                return 0

            return this.getLevelPM25Val(item.pm25)
        },
        getLevelPM25Val(val) {
            var usPm25Upper = [15.4, 40.4, 65.4, 150.4, 250.4, 1000] // change last from 500.4  to 1000
            // var usPm25Lower = [0, 15.5, 40.5, 65.5, 150.5, 250.5]

            var thPm25Upper = [25, 37, 50, 90, 1000, 1000]
            // var thPm25Lower = [0, 26, 38, 51, 91, 1000]

            var chPm25Upper = [35, 75, 115, 150, 250, 500]
            // var chPm25Lower = [0, 36, 76, 116, 151, 251]


            for (var i=0;i<6;i++)
            {
                if (this.aqi_mode == "ch" && val <= chPm25Upper[i] )
                {
                    return i +1
                }
                else if (this.aqi_mode == "us" && val <= usPm25Upper[i] )
                {
                    return i +1
                }
                else if (this.aqi_mode == "th" && val <= thPm25Upper[i] )
                {
                    return i +1
                }

            }

            return 5

        },
        getLevelAQI(item){
            if (item.status == 0) 
                return 0

            return this.getLevelAQIVal(item.us_aqi,item.th_aqi,item.ch_aqi)

        },
        getLevelAQIVal(item) {
            var th_aqi_upper = [25, 50, 100, 200, 500, 0]
            var th_aqi_lower = [0, 26, 51, 101, 201, 0]

            var ch_aqi_upper = [50, 100, 150, 200, 300, 500]
            var ch_aqi_lower = [0, 51, 101, 151, 201, 301]
        
            var us_aqi_upper = [50, 100, 150, 200, 300, 500]
            var us_aqi_lower = [0, 51, 101, 151, 201, 301]

            for (var i=0;i<6;i++)
            {
                if (this.aqi_mode == "ch" && item.ch_aqi >= ch_aqi_lower[i] && item.ch_aqi <= ch_aqi_upper[i])
                {
                    return i +1
                }
                else if (this.aqi_mode == "us" && item.us_aqi >= us_aqi_lower[i] && item.us_aqi <= us_aqi_upper[i])
                {
                    return i +1
                }
                else if (this.aqi_mode == "th" && item.th_aqi >= th_aqi_lower[i] && item.th_aqi <= th_aqi_upper[i])
                {
                    return i +1
                }

            }

            return 5

        },
        getText(item)
        {
            if (item.status == 0) 
                return "--"

            if (this.display_mode == 1)
                return item.pm25
            else
                return this.getAQI(item)

        },
        track () {
            this.$ga.page('/map')
        },
        getLevel(item){
            if (this.display_mode == 1)
                return this.getLevelPM25(item)
            else
                return this.getLevelAQI(item)
        },
        getAQI(item){

          if (this.aqi_mode == "th")
          {
            return item.th_aqi
          }
          else if (this.aqi_mode == "ch")
          {
            return item.ch_aqi
          } 
          else if (this.aqi_mode == "us")
          {
            return item.us_aqi
          }
        },
        getDescription(item){
          // US  good , moderate , usg, unhealthy , very unhelty , hazardous
          // คุณภาพอากาศดีมาก , คุณภาพอากาศดี , ปานกลาง , เริ่มมีผลกระทบต่อสุขภาพ , มีผลกระทบต่อสุขภาพ
          // Exellent , Good , Light Polluted , Moderatey Polluted , Heavily Polluted , Severely Polluted
           var th_desc = ["--","ดีมาก", "ดี", "ปานกลาง", "เริ่มมีผลต่อสุขภาพ", "มีผลกระทบต่อสุขภาพ"]
           var us_desc = ["--","ดีมาก", "ดี", "ปานกลาง", "เริ่มมีผลต่อสุขภาพ", "มีผลกระทบต่อสุขภาพ"]
           var ch_desc = ["--","ดีมาก", "ดี", "ปานกลาง", "เริ่มมีผลต่อสุขภาพ", "มีผลกระทบต่อสุขภาพ"]

            if (item.status == 0) 
            {
                return "(Offline)"
            }

            if (this.aqi_mode == "th")
            {
                return "คุณภาพอากาศ " +  th_desc[this.getLevel(item)]
            }
            else if (this.aqi_mode == "ch")
            {
                return "คุณภาพอากาศ " +  ch_desc[this.getLevel(item)]
            } 
            else if (this.aqi_mode == "us")
            {
                return "คุณภาพอากาศ " +  us_desc[this.getLevel(item)]
            }
        },
      getBgColorWithPm(pm25){
        return this.bg_colors[this.getLevelPM25Val(pm25)]

      },
      getBarChartColor(data)
      {
        var bar_color = []

        data.forEach(function (item)
        {
          bar_color.push(this.getBgColorWithPm(item));
        }.bind(this))

        return bar_color

      },
      getRandomInt () {
        return Math.floor(Math.random() * (50 - 5 + 1)) + 5
      },
      getRawPM25(){
        // this.fillData()
        axios.post(this.api_url + "pm25avg1hr",{serial:this.station.serial}).then((response)=>{

          this.datacollection = {
          labels: response.data.Legend,
          datasets: [
            {
              label: 'PM2.5 (ug/m3)',
              data: response.data.Val,
              backgroundColor: this.getBarChartColor(response.data.Val)
            }        
          ]
        }
        })

      },
      getRawAQI(){

        axios.post(this.api_url + "pm25avg24hr",{serial:this.station.serial}).then((response)=>{

          this.datacollection = {
          labels: response.data.Legend,
          datasets: [
            {
              label: 'TH AQI',
              data: response.data.Val,
              backgroundColor: this.getBarChartColor(response.data.Val)
            }        
          ]
        }
        })
      },

    }
    
}
</script>
